/* eslint-disable no-undef */
import React,{useRef, useState} from 'react'
import{VscCircleLargeFilled} from 'react-icons/vsc'
import {TiLocation} from 'react-icons/ti'
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import Container from 'react-bootstrap/Container';
import {useJsApiLoader,GoogleMap,Marker,Autocomplete,DirectionsRenderer} from '@react-google-maps/api'
import './App.css';




const center ={lat: -3.745,lng:-38.523 }

const containerStyle ={width:"100vh",height:'80vh'}
// const mobile={width:"50vh",height:'200px'}

// var w=( window.innerWidth, window.innerHeight)

// const containerStyle=(w>'576px') ? desktop : mobile


function App() {

  const[map,setMap]=useState(/** @type google.maps.googlemaps */ null)
  const[direction,setDirection]=useState(null)
  const [distance,setDistance]=useState('')

  // const [location,setLocation]=useState({
  //   origin1:'',
  //   destination1:''
  // })

  const originRef =useRef()
  const destinationRef=useRef()

  const {isLoaded}=useJsApiLoader({

   // googleMapsApiKey: 'AIzaSyDRP_BM9uCENYVcd03udkMzG0CcBfWYjLA',
   googleMapsApiKey:'AIzaSyAolXVBph__8LXk-JukgnxDUI4LPDQAsxQ',
    libraries:['places']

})

if(!isLoaded){
  return <h1>Loading...</h1>
}

const CalaculateDistance=async(e)=>{
  e.preventDefault()
  if(originRef.current.value === '' || destinationRef.current.value ===''){
    return 
  }
  const directionService = new google.maps.DirectionsService()
  const result = await directionService.route({
    origin:originRef.current.value,
    destination:destinationRef.current.value,
    travelMode:google.maps.TravelMode.DRIVING
  })
  setDirection(result)
  setDistance(result.routes[0].legs[0].distance.text)


}

const clearRoute=()=>{
  setDirection(null)
  setDistance('')
  originRef.current.value=''
  destinationRef.current.value=''
}

// const locationVal=()=>{
//   setLocation({...location,[e.target.name]:e.target.value})
// }


  return (
    <div className="App">

<Navbar bg="light" variant="light">
        <Container>
          <Navbar.Brand href="#home"><img src="https://static.wixstatic.com/media/7650b3_a6cf687f91264ae08fbd262658eb2bc6~mv2.png/v1/fill/w_168,h_70,al_c,q_85,usm_0.66_1.00_0.01,enc_auto/Graviti%20Logo.png" alt="company-logo" className="company-logo"/></Navbar.Brand>
          {/* <Nav className="me-auto">
            <Nav.Link href="#home">Home</Nav.Link>
            <Nav.Link href="#features">Features</Nav.Link>
            <Nav.Link href="#pricing">Pricing</Nav.Link>
          </Nav> */}
        </Container>
      </Navbar>
   

      
          <p className="map-para">Let's calaculate  <span>Distance</span> from Google maps</p>
        
      <div className='map-form-container'>
        

<div className='form-bg'>
        <form  className="form-location" onSubmit={CalaculateDistance}>
            <label>origin</label>
            <div className="form-text1">
                <div className='form-icon'>
            <VscCircleLargeFilled  className="origin-icon"/>
            </div>
            <Autocomplete>
            <input type="search"  className="input-form" ref={originRef}   />

            </Autocomplete>
               
            </div>

            <div className='button-calc'>
                <button className='btn btn-primary' onClick={CalaculateDistance} type="submit">Calaculate</button>
            </div>

            


                 <label>Destination</label>
            <div className="form-text1">
                <div className='form-icon-location'>
            <TiLocation />
            </div>
            <Autocomplete>
            <input type="search"  className="input-form" ref={destinationRef} />

            </Autocomplete>
               
            </div>

            <div className='button-calc1'>
                <button className='btn btn-primary' onClick={CalaculateDistance} type="submit">Calaculate</button>
            </div>
          

            <div className='distance'>
                
                <h1 className='distance-head'>Distance</h1> <h1  className='distance-value'>{distance}</h1>
            </div>

           <div>
            <p>the distance between<span></span>  and <span></span> via selected route is {distance}</p>
            </div>
        </form>
        
    </div>

    <div className="map-container">
    {/* <img src="https://img.freepik.com/premium-vector/abstract-flat-map-city-plan-town-detailed-city-map_257312-609.jpg?w=1060 " alt="world-map"/> */}
      <GoogleMap  zoom={15} center={center} mapContainerStyle={ containerStyle } className=''
      options={{
        zoomControl:false,
        streetViewControl:false,
        mapTypeControl:false,

      }}
      
      onLoad={(map)=>setMap(map)}
      >
        <Marker position={center}/>
        {direction && <DirectionsRenderer  directions={direction}/>}

      </GoogleMap>
    </div>
    </div>
     
    </div>
  );
}

export default App;
